import { NgClass } from '@angular/common';
import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
import { PREMIUM_UNLIMITED_OPTIONS } from '@core/constants';
import { PremiumUnlimitedPlan } from '@core/enums';

@Component({
    selector: 'findqo-subscription-button',
    imports: [MatButtonModule, MatIconModule, MatProgressSpinnerModule, NgClass],
    template: `
		<div class="w-100 text-center">
			<p class="heading-1 fw-400">
				<sub class="line-through">{{ paymentOptions[paymentPlan].currency + paymentOptions[paymentPlan].originalPrice }}</sub>
				<sup class="fs-14px fg-general-5 fw-500">{{ paymentOptions[paymentPlan].currency }}</sup>
				<span class="fg-general-5 fw-600">{{ paymentOptions[paymentPlan].offerPrice }}</span>
				<sup class="text-caption-2-regular fg-general-4 vat-description">{{ paymentOptions[paymentPlan].vatDescription }}</sup>
			</p>

			<div class="mt-4">
				<button
					mat-flat-button
					color="primary"
					class="btn-subscribe w-100 position-relative"
					[ngClass]="btnClass"
					[disabled]="isLoading"
					(click)="onSubscribe(paymentOptions[paymentPlan].planTierId)"
				>
					Subscribe

					@if (isLoading) {
						<mat-spinner class="position-absolute top-50 start-50 translate-middle" diameter="16"></mat-spinner>
					}
				</button>
			</div>
		</div>
	`,
    styles: `
		sub.line-through {
			text-decoration: line-through;
			font-size: 10px;
			line-height: 14px;
			color: var(--general-4);
		}

		sup {
			top: -0.7em;

			&.vat-description {
				top: -1.5em;
			}
		}

		button.btn-subscribe {
			height: 40px;
		}
	`,
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class SubscriptionButtonComponent {
	@Input() paymentPlan = PremiumUnlimitedPlan.MONTHLY;
	@Input() isLoading = false;
	@Input() btnClass?: string;
	@Output() subscribed = new EventEmitter<string>();

	readonly paymentOptions = PREMIUM_UNLIMITED_OPTIONS;

	onSubscribe(plantierId: string): void {
		this.subscribed.emit(plantierId);
	}
}
